export const IconList: Record<string, string> = {};
// https://webpack.js.org/guides/dependency-management/#require-context
const requireComponent = (require as any).context('@/components/Icon/Icons', true, /[\w]+\.svg$/);
requireComponent.keys().forEach((fileName: any) => {
  const mod = requireComponent(fileName);
  const modName = fileName.replace(/\.\//, '').replace(/\.svg/, '');
  if (!mod) return;
  IconList[modName] = mod;
});
