import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router';

const routes: Array<RouteRecordRaw> = [
  { path: '/', redirect: '/Token' },
  { path: '/Token', name: 'Token', component: () => import('../views/Token/Index.vue') },
  { path: '/Pilot', name: 'Pilot', component: () => import('../views/Pilot/Index.vue') },
  { path: '/Badge', name: 'Badge', component: () => import('../views/Badge/Index.vue') },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
