import { BigNumber, Contract, providers, utils } from 'ethers';
import WalletConnectProvider from '@walletconnect/web3-provider';
import { AbiConfig, Constants } from '@/Constants';
import mitt from 'mitt';
import { onMounted, onUnmounted, ref } from 'vue';

type ProviderEvents = {
  accountsChanged: string;
  chainChanged: number;
  disconnect: [number, string];
};
export const providerBus = mitt<ProviderEvents>();
export const CurrentWalletEnv = { wallet: '', chain: 0 };
export const useWallet = () => {
  const wallet = ref(CurrentWalletEnv.wallet);
  const chain = ref(CurrentWalletEnv.chain);
  const _wallet = (v: string) => (wallet.value = v);
  const _chain = (v: number) => (chain.value = v);
  onMounted(async () => {
    providerBus.on('accountsChanged', _wallet);
    providerBus.on('chainChanged', _chain);
    const prov = await getProvider();
    prov.getNetwork().then((net) => {
      chain.value = CurrentWalletEnv.chain = net.chainId;
    });
    prov.listAccounts().then((accs) => {
      wallet.value = CurrentWalletEnv.wallet = accs[0] || '';
    });
  });
  onUnmounted(() => {
    providerBus.off('accountsChanged', _wallet);
    providerBus.off('chainChanged', _chain);
  });
  return { wallet, chain };
};

export const getExProvider = async (): Promise<WalletConnectProvider> => {
  if (window.ethereum && window.ethereum.isMetaMask) return window.ethereum;
  const exProvider = new WalletConnectProvider({
    rpc: Constants.Chain.PRC.reduce((a, s, i) => Object.assign(a, { [i + 1]: s }), {}),
  });
  await exProvider.enable();
  return exProvider;
};

let lastProvider: providers.Web3Provider | null = null;
export const getProvider = async () => {
  if (lastProvider) return lastProvider;
  const exProvider = await getExProvider();
  exProvider.on('accountsChanged', (address: string[]) => {
    providerBus.emit('accountsChanged', address[0] || '');
  });
  exProvider.on('chainChanged', (chainId: number) => {
    providerBus.emit('chainChanged', chainId);
  });
  exProvider.on('disconnect', (code: number, reason: string) => {
    providerBus.emit('disconnect', [code, reason]);
  });
  lastProvider = new providers.Web3Provider(exProvider);
  return lastProvider;
};

export const getContractHandler = async (type: keyof typeof Constants.Contract, address = '') => {
  const provider = await getProvider();
  if (!provider) return null;
  if (!address) address = Constants.Contract[type];
  const signer = provider.getSigner();
  return new Contract(address, AbiConfig[type], signer);
};

export const ConnectWallet = async () => {
  try {
    const exProvider = await getExProvider();
    await exProvider.request({ method: 'eth_requestAccounts' });
    return true;
  } catch (e) {
    console.log(e);
    return false;
  }
};

export const Switch2Mainnet = async (): Promise<boolean> => {
  const exProvider = await getExProvider();
  try {
    await exProvider.request({
      method: 'wallet_switchEthereumChain',
      params: [{ chainId: `0x${parseInt(Constants.Chain.ID).toString(16)}` }],
    });
    return true;
  } catch (e: any) {
    if (e && 'code' in e && e.code === 4902) {
      await exProvider.request({
        method: 'wallet_addEthereumChain',
        params: [
          {
            chainId: `0x${parseInt(Constants.Chain.ID).toString(16)}`,
            rpcUrls: Constants.Chain.PRC,
            chainName: Constants.Chain.Name,
            nativeCurrency: {
              name: Constants.Chain.nativeCurrency.name,
              symbol: Constants.Chain.nativeCurrency.symbol,
              decimals: 18,
            },
          },
        ],
      });
      return Switch2Mainnet();
    }
    if (e && 'message' in e) alert(e.message);
    return false;
  }
};

export const watchTransaction = async (txHash: string, callback: (rev: any, succ: boolean) => any) => {
  const provider = await getProvider();
  return new Promise((resolve) => {
    provider.once(txHash, (transaction) => {
      callback(transaction, transaction.status === 1);
      resolve(transaction);
    });
  });
};

export const parseBigNumber = (bn: BigNumber, decimals = 2) => {
  if (!bn) return 0;
  try {
    return numberWithCommas(parseFloat(utils.formatUnits(bn)).toFixed(decimals));
  } catch (e) {
    return bn;
  }
};
export const parseBigNumber1 = (bn: BigNumber, decimals = 2) => {
  if (!bn) return 0;
  try {
    return parseFloat(utils.formatUnits(bn)).toFixed(decimals);
  } catch (e) {
    return bn;
  }
};
export const parseBigNumberNoFixed = (bn: BigNumber) => {
  if (!bn) return 0;
  try {
    return parseFloat(utils.formatUnits(bn));
  } catch (e) {
    return bn;
  }
};

function numberWithCommas(x: string) {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
}

export const ContractActionError = (e: any) => {
  if (!e) return '';
  if (typeof e === 'string') return e;
  if (typeof e !== 'object') return String(e);
  if (e.code === 4001) return ''; // 用户拒绝
  let message = e.message || '';
  if (e.data && typeof e.data === 'object') {
    if (e.data.message) message = message + ' : ' + e.data.message;
  }
  return message;
};
